<template>
  <div class="payment">
    <div class="pay_way_group">
      <div class="pay_way_title">会员注册</div>
      <van-cell-group class="payment_group">
        <van-field v-model="data.name" label="姓名" placeholder="请输入真实姓名" :error="errorInfo.name"
                   @focus="onFocus('name')"/>
        <van-field v-model="data.bank" label="开户银行" placeholder="请选择开户银行" readonly @click="showBank = true"
                   :error="errorInfo.bank" @focus="onFocus('bank')"/>
        <van-field v-model="data.card_no" type="number" label="银行卡号" placeholder="请输入卡号" :error="errorInfo.card_no"
                   @focus="onFocus('card_no')" error-message="请填写本人的正确的卡号，作为任务收款用"/>
        <van-field v-model="data.mobile" label="手机号码" placeholder="请输入手机号码" :error="errorInfo.mobile" error-message="请填写本人实名认证的手机号，否则无法注册通过！"/>
        <van-field v-model="data.password" type="password" label="登录密码" placeholder="请输入6位以上数字或字母"
                   :error="errorInfo.password" @focus="onFocus('password')"/>
        <van-field v-model="data.repassword" type="password" label="确认密码" placeholder="请再次输入密码"
                   :error="errorInfo.repassword" @focus="onFocus('repassword')"/>
        <van-field v-model="data.v_code" label="手机验证码" placeholder="请输入4位手机验证码" :error="errorInfo.v_code"
                   @focus="onFocus('v_code')">
          <countdown v-if="counting" slot="button" :time="60000" @countdownend="countdownend" class="countdown">
            <template slot-scope="props">{{ +props.seconds || 60 }}秒后获取</template>
          </countdown>
          <van-button v-else slot="button" size="small" type="primary" @click="getCode">发送验证码</van-button>
        </van-field>
      </van-cell-group>
    </div>
    <input @change="fileChanged" type="file" accept="image/jpg, image/jpeg" ref="file" style="display: none"/>
    <div class="register-btn">
      <van-button size="large" @click="next" type="danger">
        提交注册
      </van-button>
    </div>
    <van-popup v-model="showBank" position="bottom">
      <van-picker
        show-toolbar
        :columns="bankColumns"
        title="选择银行"
        @cancel="showBank = false"
        @confirm="onBankConfirm"/>
    </van-popup>
  </div>
</template>

<script>

  import { Cell, CellGroup, Area, Field, Popup, Button, RadioGroup, Radio, Row, Col, Toast, Picker } from "vant";
  // import AddressEditDetail from "vant/lib/address-edit/Detail";
  import { isObj } from "vant/lib/utils";
  import validateMobile from "vant/lib/utils/validate/mobile";
  // import areaList from "../../../assets/data/area";
  import bankList from "../../../assets/data/bank";

  import { REGISTER, USER_SENDCODE } from "@/api/user";

  const data = {
    referrer: "",//介绍人ID
    mobile: "",
    name: "",
    province: "",
    city: "",
    county: "",
    area_code: "",
    address_detail: "",
    sex: 1,
    age: 0,
    bank: "",
    card_no: "",
    qq: "",
    wechat: "",
    password: "",
    repassword: "",
    v_code: ""
  };

  const bankColumns = bankList["bank_list"];

  export default {

    components: {
      [Cell.name]: Cell,
      [CellGroup.name]: CellGroup,
      [Area.name]: Area,
      [Field.name]: Field,
      [Popup.name]: Popup,
      [Button.name]: Button,
      [RadioGroup.name]: RadioGroup,
      [Radio.name]: Radio,
      // [AddressEditDetail.name]: AddressEditDetail,
      [Row.name]: Row,
      [Col.name]: Col,
      [Toast.name]: Toast,
      [Picker.name]: Picker,
      validateMobile
    },

    data() {
      return {
        // showArea: false,
        // showGender: false,
        showBank: false,
        data,
        wxpay: "",
        files: [],
        // sexColumns: ["保密", "男", "女"],
        bankColumns,
        // areaList,
        detailFocused: false,
        counting: false,
        errorInfo: {
          name: false,
          bank:false,
          card_no:false,
          password: false,
          repassword: false,
          v_code: false,
          mobile: false
        }
      };
    },

    created() {
      this.data.mobile = this.mobile;
      this.data.referrer = this.referrer;
    },

    computed: {

    },

    methods: {

      onFocus(key) {
        this.errorInfo[key] = false;
        this.detailFocused = key === "address_detail";
      },

      fileSelect() {
        this.$refs.file && this.$refs.file.click();
      },
      fileChanged(e) {
        let list = this.$refs.file.files;
        if (this.disabled || !list.length) {
          return;
        }
        let f = list[0];
        if (!this.isContain(f)) {
          const item = {
            name: f.name,
            size: f.size,
            src: "",
            file: f
          };
          this.html5Reader(f, item);
          this.files = [];
          this.files.push(item);
        }
        this.$refs.file.value = "";
      },
      // 将图片文件转成BASE64格式
      html5Reader(file, item) {
        const reader = new FileReader();
        reader.onload = (e) => {
          item.src = e.target.result;
          this.wxpay = item.src;
        };
        reader.readAsDataURL(file);
      },

      isContain(file) {
        return this.files.find((item) => item.name === file.name && item.size === file.size);
      },

      // onChangeDetail(val) {
      //   this.data.address_detail = val;
      // },

      // onAreaConfirm(values) {
      //   this.showArea = false;
      //   if(values[2].code==-1){
      //     return;
      //   }
      //   this.data.area_code = values[2].code;
      //   this.assignAreaValues(values);
      // },

      // onGenderConfirm(value, index) {
      //   this.showGender = false;
      //   this.data.sex = index;
      // },

      onBankConfirm(value, index) {
        this.showBank = false;
        this.data.bank = value.text;
      },

      // assignAreaValues(values) {
      //   Object.assign(this.data, {
      //     province: values[0].name,
      //     city: values[1].name,
      //     county: values[2].name
      //   });
      // },
      //
      // getAreaContainer() {
      //   return document.body;
      // },

      next() {
        // if (this.files.length != 1 && (!this.data.bank || !this.data.card_no)) {
        //   Toast("微信收款码和银行账号必须提供一种");
        //   return;
        // }
        let taobaoUserId = this.$route.params.taobaoUserId;
        let orderNo = this.$route.params.orderNo;
        let shopId = this.$route.params.shopId;
        if(!taobaoUserId){
          Toast('淘宝ID丢失，请返回重新验证订单');
          return;
        }
        let items = [
          "name",
          "bank",
          "card_no",
          "mobile",
          "password",
          "repassword",
          "v_code"];
        const isValid = items.every(item => {
          let msg = this.getErrorMessage(item);
          if (msg) {
            this.errorInfo[item] = true;
            Toast(msg);
          }
          return !msg;
        });
        if (isValid && !this.isSaving) {
          let formdata = new FormData();
          formdata.append("name", this.data.name);
          formdata.append("bank", this.data.bank);
          formdata.append("bank_number", this.data.card_no);
          formdata.append("mobile", this.data.mobile);
          formdata.append("v_code", this.data.v_code);
          formdata.append("password", this.data.password);
          formdata.append("taobaoUserId", taobaoUserId);
          formdata.append("orderNo", orderNo);
          formdata.append("shopId", shopId);
          if(this.files.length == 1){
            formdata.append("wxpay", this.files[0].file);
          }
          this.$reqPost(REGISTER, formdata).then(res => {
            this.regSuccess(res);
          }).catch(err => {
            this.regSuccess(err);
          });
        }
      },

      regSuccess(res) {
        if (!res.data) {
          return;
        }
        if (res.data.code != 0) {
          let msg = res.data.msg ? res.data.msg : "注册失败";
          Toast.fail(msg);
          return;
        }
        Toast.success("注册成功，跳转登录");
        this.$router.replace({ name: "login" });
      },

      getErrorMessage(key) {
        const value = this.data[key];
        switch (key) {
          case "name":
            return value ? "" : "请填写真实姓名";
          case "mobile":
            if(!value) return "请填写手机号码";
            return validateMobile(value) ? "" : "请输入正确的手机号码";
          case "area_code":
            return value ? "" : "请选择收获地址";
          case "address_detail":
            return value ? "" : "请填写详细收获地址";
          case "age":
            return value ? "" : "请填写年龄";
          case "bank":
            return value ? "" : "请选择银行";
          case "card_no" :
            return value ? "" : "请填写银行卡号";
          case "password" :
            return value ? "" : "请填写密码";
          case "repassword":
            let msg = value ? "" : "请再次输入密码";
            if (!msg) {
              msg = (value == this.data["password"]) ? "" : "两次输入的密码不一致";
            }
            return msg;
          case "v_code" :
            return value ? "" : "请填写验证码";
        }
      },

      /****获取验证码****/
      getCode() {
        let msg = this.getErrorMessage("mobile");
        if (msg) {
          Toast(msg);
          return;
        }
        if (!this.counting) {
          this.$reqPost(USER_SENDCODE, {
            mobile: this.data.mobile,
            operation: "register"
          }).then((res) => {
            console.dir(res);
            if(res.data.code == 0){
              this.$toast.success("发送成功");
              this.counting = true;
            }else{
              this.$toast.fail(res.data.msg)
            }
          }).catch((err) => {
            console.dir(err);
            if(err.data.code == 0){
              this.$toast.success("发送成功");
              this.counting = true;
            }else{
              this.$toast.fail(err.data.msg)
            }
          });
        }
      },

      countdownend() {
        this.counting = false;
      }

    }
  };
</script>

<style>
  .van-cell__value {
    overflow: hidden;
    text-align: left;
    vertical-align: middle;
  }
</style>

<style lang="scss" scoped>

  .pay_way_group {
    background-color: #ffffff;
  }

  .pay_way_title {
    padding: 15px;
    background-color: #fff;
  }

  div.register_view {
    background-color: #fff;
    padding-top: 30px;
  }

  div.register_submit {
    padding-top: 30px;
    padding-bottom: 20px;
  }

  .register_footer {
    text-align: right;
    color: $font-color-gray;
  }

  .register-btn {
    padding: 10px;
  }

  .countdown {
    color: #4b0;
  }

  .card-item {
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
    /*background-color: #f2f2f2;*/
  }

  .id_card_upload {
    background-color: #fff;
    padding: 15px 0px;
    > div {
      margin-bottom: 15px;
    }
  }

  .title {
    margin: 15px;
  }

  .id_card_row {
    > div {
      text-align: center;
    }
    .text-desc {
      margin-top: 8px;
      margin-bottom: 8px;
    }
  }

  .upload_box .upload_wrap {
    position: relative;
    border: 1px dashed $gray;
    min-height: 100px;
    box-sizing: border-box;
    padding: 5px;
    text-align: center;
    img {
      max-width: 100%;
      height: 120px;
    }
    .add_btn {
      position: absolute;
      top: 50%;
      left: 50%;
      font-size: 16px;
      color: $gray;
      transform: translate(-50%, -50%);
      i {
        font-size: 24px;
      }
    }
  }

</style>




